import React from 'react';
import { Navbar, NavbarBrand } from 'reactstrap';
// styles
import './_navbar.scss';
import './_navbar-mq.scss';
// components
import Menu from '../Menu';
import Icon from '../Icon';
// import Icon from '../Icon';
// icons
// import Email from '../Icon/email.icon';
import IconHome from '../Icon/home.icon';

const NavBar = props => (
    <Navbar className='flex-row flex-lg-column justify-content-between align-items-center'>
        <NavbarBrand className='m-0 text-center'
                     href="/">
            {/*<Icon className="icon" icon={IconHome}/>*/}
            <span>Erich</span>
        </NavbarBrand>
        <Menu/>
        <div className="email text-md-center text-right">
            <a href="mailto:erichfgb@gmail.com" className='h1'>@</a>
        </div>
    </Navbar>
);

export default NavBar;



