import React from 'react';
import Link from "gatsby-link";
import { Nav, NavItem } from 'reactstrap';
// styles
import './_menu.scss';
// components
import Icon from '../Icon';
// icons svg
// skills
import StrongColor from '../Icon/strong-color.icon';
import StrongBlack from '../Icon/strong-black.icon';
// works
import WorksColor from '../Icon/works-color.icon';
import WorksBlack from '../Icon/works-black.icon';

const Menu = props => (
    <Nav navbar className="flex-row flex-lg-column">
        <NavItem>
            <Link className="nav-link"
                  to='/skills'
                  activeClassName='active'>
                <span className="figure">
                    <Icon className="icon" icon={StrongBlack}/>
                    <Icon className="icon icon--color" icon={StrongColor}/>
                </span>
                <span className="text">Skills</span>
            </Link>
        </NavItem>
        {/*<NavItem>*/}
            {/*<Link className="nav-link"*/}
                  {/*to='/works'*/}
                  {/*activeClassName='active'>*/}
                {/*<span className="figure">*/}
                    {/*<Icon className="icon" icon={WorksBlack}/>*/}
                    {/*<Icon className="icon icon--color" icon={WorksColor}/>*/}
                {/*</span>*/}
                {/*<span className="text">Portfolio</span>*/}
            {/*</Link>*/}
        {/*</NavItem>*/}
    </Nav>
);

export default Menu;


